const StyledServeses = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 100px 100px;
`
const ServSectionTitle = styled.h2`
  color: ${theme.colors.primaryFont}
  width: 100%;
  min-width: 200px;
  text-align: center;
  font-size: 40px;
  font-weight: 600;
  margin: 100px auto 0;
  display: inline-block;
  position: relative;
  z-index: 2;
  text-transform: uppercase;

  span {
    color: ${theme.colors.accent}
  }

  &::after {
    content: "Поиск кролика";
    position: absolute;
    transform: translate(-310px, -30px) scale(2.25);
    transform-origin: center;
    color: ${theme.colors.primaryFont};
    opacity: 0.06;
    z-index: 1;
    bottom: 0;
    width: 100%;
  }
`

const Text = styled.p`
  margin-top: 33px;
  max-width: 1050px;
  width: 100%;
  color: ${theme.colors.secondaryFont};
  text-align: center;
  font-size: 15px;
  font-weight: 400;
`

const Container = styled.div`
  margin-top: 45px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 50px;
  justify-content: space-around;
`

type SvgPropsType= {
    bgColor?:string
}

const MinContainer = styled.div<SvgPropsType>`
  width: 357px;
  height: 90px;
  display: flex;
  position: relative;
  z-index: 1;

  svg {
    width: 52px;
    height: 56px;
    position: absolute;
    left: 25px;
    top: 17px
  }

  &:after {
    content: "";
    width: 102px;
    height: 90px;
    border-radius: 7px;
    background-color: ${props => props.bgColor || "#E2DEFF"};
    position: absolute;
    z-index: -1;
    top: 0;
  }

  h3 {
    color: ${theme.colors.primaryFont};
    text-align: left;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
  }

  p {
    color: ${theme.colors.secondaryFont};
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    margin-top: 12px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    
    &:hover{
      -webkit-line-clamp: 3;
    }
  }

  div {
    padding-left: 130px;
  }
`